  position: relative;
  &:before {
    position: absolute;
    height: 97%;
    content: '';
    left: -10px;
    width: 3px;
    border-radius: 4px;
    top: 50%;
    transform: translateY(-50%);
    opacity: 0.8;
  }

  &.voc-row:before {
    background: #FF545E;
  }

  &.vod-row:before {
    background: #00ADCF;
  }
}
